import isArray from './isArray';
import isArrayLike from './isArrayLike';
import isValid from './isValid';

/**
 * 
 * @param arr 
 * @returns 
 */
export function array<T>(arr?: T | T[]): T[] {
  if (isValid(arr)) {
    return isArray(arr) ? arr : [arr];
  }
  return [];
}

/**
 *  undefined
 * @param val any
 * @returns
 */
export function last<T>(val: T | T[]): T | undefined {
  if (isArrayLike(val)) {
    const arr = val as ArrayLike<any>;
    return arr[arr.length - 1];
  }
  return undefined;
}

/**
 *  0
 * @param arr
 * @returns
 */
export const span = (arr: number[]) => {
  if (arr.length <= 1) {
    return 0;
  }
  return (last(arr) as number) - arr[0];
};

/**
 * 
 * @param arr 
 * @param compareFn  a > b a < b 0 
 * @returns
 */
export const maxInArray = <T>(arr: T[], compareFn?: (a: T, b: T) => number): T | undefined => {
  if (arr.length === 0) {
    return undefined;
  }
  let max = arr[0];
  for (let i = 1; i < arr.length; i++) {
    const value = arr[i];
    if ((compareFn?.(value, max) ?? (value as number) - (max as number)) > 0) {
      max = value;
    }
  }
  return max;
};

/**
 * 
 * @param arr 
 * @param compareFn  a > b a < b 0 
 * @returns
 */
export const minInArray = <T>(arr: T[], compareFn?: (a: T, b: T) => number): T | undefined => {
  if (arr.length === 0) {
    return undefined;
  }
  let min = arr[0];
  for (let i = 1; i < arr.length; i++) {
    const value = arr[i];
    if ((compareFn?.(value, min) ?? (value as number) - (min as number)) < 0) {
      min = value;
    }
  }
  return min;
};

/**
 * 
 * @param a
 * @param b
 * @returns
 */
export function arrayEqual(a: any, b: any): boolean {
  if (!isArray(a) || !isArray(b)) {
    return false;
  }
  if (a.length !== b.length) {
    return false;
  }
  for (let i = 0; i < a.length; i++) {
    if (a[i] !== b[i]) {
      return false;
    }
  }
  return true;
}

/**
 * 
 * @param arr
 * @returns
 */
export function uniqArray<T>(arr: T | T[]): T | T[] {
  if (!arr || !isArray(arr)) {
    return arr;
  }
  return Array.from(new Set(array(arr)));
}

// Based on http://jsfromhell.com/array/shuffle
export function shuffleArray<T>(arr: T[], random: () => number = Math.random): T[] {
  let j;
  let x;
  let i = arr.length;

  while (i) {
    j = Math.floor(random() * i);
    x = arr[--i];
    arr[i] = arr[j];
    arr[j] = x;
  }
  return arr;
}

/**
 *  [input]
 * @param arr
 * @returns
 */
export function flattenArray(arr: any): any[] {
  if (!isArray(arr)) {
    return [arr];
  }
  const result = [];
  for (const value of arr) {
    result.push(...flattenArray(value));
  }
  return result;
}
